const state = {
    wishlist: {},
    keyword: "",
    movieRentals: [],
    moviePurchases: [],


}

const getters = {

    getKeyword: (state) => state.keyword,
    getMoviePurchases: (state) => {
        return getByKeyword(state.moviePurchases, state.keyword)
    },
    getMovieRentals: (state) => {
        return getByKeyword(state.movieRentals, state.keyword)
    },
    getWishList: (state) => state.wishlist
}

const actions = {

    async fetchWishList({ commit }, { authData }) {

        const response = await new Promise((resolve, reject) => {
            var AuthHeaders = new Headers();
            AuthHeaders.append("Authorization", `Bearer ${authData.access_token}`);
            AuthHeaders.append("Content-Type", "application/json");

            var requestOptions = {
                method: "GET",
                headers: AuthHeaders,
                redirect: "follow",
            };
            fetch(
                `https://hfozhlnmfh.execute-api.ap-southeast-2.amazonaws.com/v1/ttvbo/users/${authData.uuid}/wishlist/assets?deviceType=telstratv`,
                requestOptions
            )
                .then((response) => response.json())
                .then((result) => resolve(result))
                .catch((error) => reject(error));
        });
        commit('setWishList', response)
    },
    async fetchMovieRentals({ commit }, { authData }) {
        let movieRentals = []
        let moviePurchases = []
        if (localStorage.movieRentals) {
            movieRentals = JSON.parse(localStorage.movieRentals)
            commit('setMovieRentals', movieRentals)
        }
        if (localStorage.moviePurchases) {
            moviePurchases = JSON.parse(localStorage.moviePurchases)
            commit('setMoviePurchases', moviePurchases)
        }
        let LibraryList = await new Promise((resolve, reject) => {
            var AuthHeaders = new Headers();
            AuthHeaders.append("Authorization", `Bearer ${authData.access_token}`);
            AuthHeaders.append("Content-Type", "application/json");
            //{"movies":{"rentals":[],"purchases":[]},"series":{"rentals":[],"purchases":[]}}
            var requestOptions = {
                method: "GET",
                headers: AuthHeaders,
                redirect: "follow",
            };
            fetch(
                `https://hfozhlnmfh.execute-api.ap-southeast-2.amazonaws.com/v1/ttvbo/users/${authData.uuid}/library?attributes=id,images,displayTitle,type,availability,videos`,
                requestOptions
            )
                .then((response) => response.json())
                .then((result) => resolve(result))
                .catch((error) => reject(error));
        });

        movieRentals = LibraryList.movies.rentals;
        localStorage.movieRentals = JSON.stringify(movieRentals);
        moviePurchases = LibraryList.movies.purchases;
        localStorage.moviePurchases = JSON.stringify(moviePurchases);

        commit('setMovieRentals', movieRentals)
        commit('setMoviePurchases', moviePurchases)




    }
}

const mutations = {
    setWishList: (state, wishlist) => (state.wishlist = wishlist),
    setMovieRentals: (state, movieRentals) => (state.movieRentals = movieRentals),
    setMoviePurchases: (state, moviePurchases) => (state.moviePurchases = moviePurchases),
    updateKeyword: (state, keyword) => (state.keyword = keyword),
}

export default {
    state,
    getters,
    actions,
    mutations
}

const getByKeyword = (list, keyword) => {
    const search = keyword.trim().toLowerCase()
    if (!search.length) return list
    return list.filter(item => item.displayTitle.toLowerCase().indexOf(search) > -1)
}